const initState = {
	infoError: null
}

const InfoReducer = (state = initState, action) => {
	switch(action.type){
		case "CREATE_INFO_SUCESS":
			console.log('create info sucess');
			return {
				...state,
				infoError: null
			}
		case "CREATE_INFO_ERR":
			console.log('create info error');
			return{
				...state,
				infoError: "Create info Error"
			}
		default:
			return state
	}
}

export default InfoReducer