import React from 'react'
import moment from 'moment'
import { Col, Card, CardBody, CardTitle, CardText } from 'reactstrap'
class KanbanSummary extends React.Component{
	render(){
		const { data } = this.props
		const colorStatus = data.status === "progress" ? "yellow" : "green";
		console.log(data)
		if(data){
			return(
			<Col lg='3'>
				<Card className='mb-3' key={data.id} style={{borderLeft: '2px solid '+ colorStatus}}>
					<CardBody>
						<CardTitle> {data.title} </CardTitle> 
						<CardText> {data.content} </CardText> 
						<small> <span> Deadline: </span> {data.deadline} </small>
						<small> <span> Created At: </span>  {moment(data.createdAt.toDate().toString()).calendar()} </small>
					</CardBody>
					<div className="CheckBox">
						<input type='checkbox' />
					</div>
				</Card>
			</Col>
			)
		}else{
			return(
				<div>
					loading
				</div>
			)
		}
		
	}
}

export default KanbanSummary