import React from 'react'
import { Col, Card, CardBody, CardTitle, CardText } from 'reactstrap'
import { connect } from 'react-redux'
import moment from 'moment'
class RequestSummary extends React.Component{
	render(){
		const { request, auth } = this.props
		console.log(request)
		const statusColor = request.status === "waiting" ? "green" : "yellow";
		const UserView = request.email === auth.email ? <Card className='mb-3' style={{borderLeft: '5px solid'+' '+statusColor}}>
						<CardBody>	
							<CardTitle>  {request.title} </CardTitle>
							<CardText>  {request.content}  </CardText>
							<small> request by: {request.authorFirstName + ' ' + request.authorLastName} </small>
						</CardBody>
					</Card>	
					:
					null
					;

		if(auth.uid === "cAcDFLhbBJeYOElvXnDQy99tI7s1"){
			return(
				<Col lg='12'>
					<Card className='mb-3' style={{borderLeft: '5px solid'+' '+statusColor}}>
						<CardBody>	
							<CardTitle> {request.title} </CardTitle>
							<CardText>  {request.content}  </CardText>
							<small> request by: {request.authorFirstName + ' ' + request.authorLastName} </small>
							
						</CardBody>
					</Card>	
				</Col>
			)
		}else{
			return(
				<Col lg='12'>
					{UserView}
				</Col>
			)
		}
	}
}

const mapStateToProps = (state) => {

	return{
		auth: state.firebase.auth
	}
}

export default connect(mapStateToProps)(RequestSummary)