const initState = {
	authError: null
}

const authReducer = (state = initState, action) => {
	switch(action.type){
		case "SIGNIN_SUCCESS":
			console.log('signin_success');
			return{
				...state,
				authError: null
			}
		case "SIGNIN_ERROR":
			console.log('signin_error');
			return{
				...state,
				authError: "SignIn Error"
			}
		case "SIGNOUT_SUCCESS":
			console.log('signout sucess');
			return{
				...state,
				authError: null
			}

		case "SIGNUP_SUCCESS":
			console.log('signup sucess');
			return{
				...state,
				authError: null
			}	

		case "SIGNUP_ERROR":
			console.log('signup_error');
			return{
				...state,
				authError: "signup Error"
			}	
		default:
			return state
	}
}

export default authReducer