import React, { Component } from 'react';
import TopNav from './component/layout/TopNav'
import SideNav from './component/layout/SideNav'
import Dashboard from './component/content/Dashboard'
import RequestPage from './component/content/requests/RequestPage'
import KanbanPage from './component/content/kanban/KanbanPage'
import SignIn from './component/auth/SignIn'
import SignUp from './component/auth/SignUp'
//Admin Menu
import DownloadRequest from './component/content/adminMenu/downloadRequests/DownloadRequests'
import { connect } from 'react-redux'
import { BrowserRouter, Route, Switch } from 'react-router-dom'

//FONTAWESOME
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPowerOff,faBars, faAngleRight, faClipboardList, faClipboardCheck, faBookReader, faGamepad } from '@fortawesome/free-solid-svg-icons'

library.add(faPowerOff,faBars, faAngleRight, faClipboardList, faClipboardCheck, faBookReader, faGamepad) 

class App extends Component {
  render() {
    const { auth } = this.props 
    const viewNav = auth.uid == null ? null
        : 
         <div className="Link_Nav">
          <TopNav />
          <SideNav />
        </div>
        ;

    const viewLinkContent = auth.uid != null ? <div className='Content'>
            <Switch>
              <Route path='/' component={Dashboard} exact />
              <Route path='/requests' component={RequestPage} />
              <Route path='/kanban' component={KanbanPage} />
              <Route path='/signin' component={SignIn} />
              <Route path='/signup' component={SignUp} />
              <Route path='/downloadRequests' component={DownloadRequest} />
            </Switch>
          </div>
          :
          <div className='Content_Auth'>
            <Switch>
              <Route path='/' component={Dashboard} exact/>
              <Route path='/requests' component={RequestPage} />
              <Route path='/kanban' component={KanbanPage} />
              <Route path='/signin' component={SignIn} />
              <Route path='/signup' component={SignUp} />
              <Route path='/downloadRequests' component={DownloadRequest} />
            </Switch>
          </div>
          ;

    return (
      <BrowserRouter>
        <div className="App">
          {viewNav}
          {viewLinkContent}
        </div>
      </BrowserRouter>
    );
  }
}

const mapStateToProps = (state) => {
  return{
    auth: state.firebase.auth
  }
}

export default connect(mapStateToProps)(App);
