import React from 'react'
import { createKanban } from './../../../store/actions/KanbanActions'
import { connect } from 'react-redux'

import { Row, Col, Card, CardHeader, CardBody, Form, FormGroup, Label, Input, Button } from 'reactstrap'
class CreateKanban extends React.Component{
	state = {
		title: '',
		content: '',
		deadline: ''
	}

	onChange = (e) => {
		this.setState({
			[e.target.id]: e.target.value
		})
	}

	onSubmit = (e) => {
		e.preventDefault();
		this.props.createKanban(this.state)
	}
	render(){
		console.log(this.state)
		return(
			<Col lg='12'>
				<Card className='mb-3'>
					<CardHeader> KanBan </CardHeader>
					<CardBody>
						<Form onSubmit={this.onSubmit}>
							<Row form>
					          <Col md={3}>
					            <FormGroup>
					              <Label for="exampleCity">Title</Label>
					              <Input type="text" name="city" id="title" onChange={this.onChange}/>
					            </FormGroup>
					          </Col>
					          <Col md={6}>
					            <FormGroup>
					              <Label for="exampleState">Job Desc</Label>
					              <Input type="text" name="state" id="content" onChange={this.onChange}/>
					            </FormGroup>
					          </Col>
					          <Col md={2}>
					            <FormGroup>
					              <Label for="exampleZip">Dead Line</Label>
					              <Input type="date" name="zip" id="deadline" onChange={this.onChange}/>
					            </FormGroup>  
					          </Col>
					          <Col md={1}>
					          	<Label for="exampleZip">&nbsp;</Label>
					            <Button block> Add </Button>
					          </Col>
					        </Row>
					    </Form>
					</CardBody>
				</Card>
			</Col>
		)
	}
}

const mapDispatchToProps = (dispatch) => {
	return{
		createKanban: (kbn) => dispatch(createKanban(kbn))
	}
}

export default connect(null, mapDispatchToProps)(CreateKanban)