import React from 'react'
import { signIn } from './../../store/actions/authActions'
import { connect } from 'react-redux'
import { Link, Redirect } from 'react-router-dom'
import { Container, Row, Col, Card, CardBody, CardTitle, CardText, Form, FormGroup, Label, Input, Button } from 'reactstrap'
class SignIn extends React.Component{
	state = {
		email: '',
		password: '',
		nowDate: null
	}

	onChange = (e) => {
		this.setState({
			[e.target.id]: e.target.value
		})
	}

	onSubmit = (e) => {
		e.preventDefault();
		this.props.signIn(this.state)
	}

	componentDidMount(){
		setInterval(()=>{
			this.setState({
				nowDate: new Date().toLocaleTimeString()
			})
		}, 1000)
	}
	render(){
		const { nowDate } = this.state
		const { auth } = this.props
		if(auth.uid != null) return <Redirect to='/' />
		return(
			<div className='SignIn'>
				<Container fluid>
					<Row>
						<Col lg='5' md='5' sm='8' className='mx-auto'>
							<Card  className='mb-3'>
								<CardBody>
									<div className='Header_SignIn text-center'>
										<CardTitle> Portal </CardTitle>
										<div className='Box_Clock'>
											<CardText> {nowDate} </CardText>
										</div>
									</div>
									<div className='Form_SignIn'>
										<Form onSubmit={this.onSubmit}>
											<FormGroup>
												<Label> &nbsp; </Label>
												<Input type='teks' className="inputText" onChange={this.onChange} id="email" required/>
												<span className="floating-label"> example@gmail.com </span>
											</FormGroup>
											<FormGroup>
												<Label> &nbsp; </Label>
												<Input type='password' className="inputText" onChange={this.onChange} id="password" required/>
												<span className="floating-label"> password</span>
											</FormGroup>
											<FormGroup className='text-center'>
												<Button block> Sign In </Button>
											</FormGroup>
										</Form>
									</div>
									<div className='SignUp_Link'>
										<p> Don't have account ? <Link to='/signup'> SIgn Up </Link> </p>
									</div>
								</CardBody>
							</Card>
							<div className='Blank Space'>
								
							</div>
						</Col>
					</Row>
				</Container>
			</div>
		)
	}
}

const mapStateToProps = (state) => {
	return{
		auth: state.firebase.auth
	}
}

const mapDispatchToProps = (dispatch) => {
	return{
		signIn: (credentials) => dispatch(signIn(credentials))
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(SignIn)