class Dropdown_Menu extends React.Component{
	state = {
		isExpanded: false
	}

	Toggle = (e) =>{
		console.log(this.refs.inner.clientHeight);
		this.setState({
			isExpanded: !this.state.isExpanded,
			height: this.refs.inner.clientHeight
		})
	}
	render(){
		const { title,children,icon } = this.props
		const { isExpanded, height } = this.state
		const currentHeight = isExpanded ? height : '0';
		const currentActive = isExpanded ? 'active' : ' ';
		return(
			<div className="Dropdown_Menu">
				<li onClick={this.Toggle} style={{color: "#ffffff"}} className={` ` + ' ' + currentActive}> 
					<FontAwesomeIcon icon={icon} className='Icon_Menu'/>
					<a> {title} </a>
					<FontAwesomeIcon icon='angle-right' className='Icon_Dropdown'/>
				</li>
				<div className="Menu_Dropdown" style={{height: currentHeight+'px'}}>
					<div className="Menu_List" ref="inner"> 
						<ul>
							{children}
						</ul>
					</div>
				</div>
			</div>
		)
	}
}