import React from 'react'
import { createRequest } from './../../../store/actions/RequestActions'
import { Card, CardBody, CardHeader, Button, Form, FormGroup, Label, CustomInput } from 'reactstrap'
import { connect } from 'react-redux'
class CreateRequests extends React.Component{
	state = {
		title: '',
		content: ''
	}

	onChange = (e) => {
		this.setState({
			[e.target.id]: e.target.value
		})
	}

	onSubmit = (e) => {
		e.preventDefault();
		this.props.createRequest(this.state)
	}
	render(){
		const optionRequest = ['Hardware', 'Software'];
		return(
			<Card className='mb-3'>
				<CardHeader> Form Request </CardHeader>
				<CardBody>
					<Form onSubmit={this.onSubmit}>
						<FormGroup>
				          <Label for="exampleCustomSelect">Category</Label>
				          <CustomInput type="select" id="title" name="customSelect" onChange={this.onChange}>
				            <option value="">Select</option>
				            {optionRequest.map((data)=>{
				            	return(
				            		<option key={data.id}>{data}</option>
				            	)
				            })}
				          </CustomInput>
				        </FormGroup>
				        <FormGroup>
				          <Label for="exampleCustomSelect">Description</Label>
				          <textarea type='teks' className='form-control' id="content" onChange={this.onChange}/>
				        </FormGroup>
				        <FormGroup>
				          <Button block> Request </Button>
				        </FormGroup>
				    </Form>
				</CardBody>
			</Card>
		)
	}
}

const mapDispatchToProps = (dispatch) => {
	return {
		createRequest: (request) => dispatch(createRequest(request))
	}
}
export default connect(null, mapDispatchToProps)(CreateRequests)