import React from 'react'
import { Col, Card, CardBody, CardTitle, CardText } from 'reactstrap'

class InfoSummary extends React.Component{
	render(){
		const { information } = this.props
		console.log(information)
		if(information){
			return(
				<Col lg='12'>
					{information.map((info)=>{
						return(
						<Card key={info.id}  className='mb-3'>
							<CardBody>
								<CardTitle> {info.title} </CardTitle>
								<CardText> {info.content} </CardText>
								<small> <span> Published by: </span> {info.authorFirstName + ' ' + info.authorLastName} </small>
								<small> <span> Date :  </span>  {info.date} </small>
							</CardBody>
						</Card>
						)
					})}
				</Col>
			)
		}else{
			return(
				<div>
					loading
				</div>
			)
		}
		
	}
}

export default InfoSummary