import React from 'react'
import KanbanSummary from './KanbanSummary'
import CreateKanban from './CreateKanban'
import { Redirect } from 'react-router-dom'
import { Container, Row } from 'reactstrap'
import { connect } from 'react-redux'
import { compose } from 'redux'
import { firestoreConnect } from 'react-redux-firebase'


class KanbanPage extends React.Component{
	render(){
		const { kanban, auth } = this.props
		if(auth.uid == null) return <Redirect to='/signin' />
		return(
			<div  className="KanbanPage">
				<Container fluid>
					<Row>
						<CreateKanban />
					</Row>
					<Row>
					{kanban && kanban.map((data)=>{
						return(
							<KanbanSummary data={data}/>
						)
					})}
					</Row>
				</Container>
			</div>
		)
	}
}

const mapStateToProps = (state) => {
	return{
		kanban: state.firestore.ordered.kanban,
		auth: state.firebase.auth
	}
}
export default compose(
	connect(mapStateToProps),
	firestoreConnect([{
		collection: "kanban", orderBy: ['createdAt', 'desc']
	}])
)(KanbanPage)