import React from 'react'
import { signOut } from './../../store/actions/authActions'
import { Navbar, NavbarBrand } from 'reactstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'

class TopNav extends React.Component{
	ToggleSlider = (e) => {
		const MenuSidenav = document.getElementsByClassName('MenuSidenav')[0];
		const ToggleMobile = document.getElementsByClassName('ToggleMobile')[0];
			  MenuSidenav.classList.toggle('MenuSidenav_Slider');
			  ToggleMobile.classList.toggle('ToggleMovile_Slider');
	}
	render(){
		return(
			<div className='TopNav'>
				<Navbar light expand="md">
	         		<NavbarBrand onClick={this.ToggleSlider}> 
	         			<FontAwesomeIcon icon='bars' />
	         		</NavbarBrand>
	         		<a onClick={this.props.signOut} className='LogOut'> 
	         			<FontAwesomeIcon icon="power-off" />
	         	 	</a>
	         	</Navbar>
         	</div>
		)
	}
}

const mapDispatchToProps = (dispatch) => {
	return{
		signOut: () => dispatch(signOut())
	}
}

export default connect(null, mapDispatchToProps)(TopNav)