import React from 'react'
import { Container, Row, Col, Card, CardHeader, CardBody, Table, Button } from 'reactstrap'
import { Redirect } from 'react-router-dom'
import { connect } from 'react-redux'
import { firestoreConnect } from 'react-redux-firebase'
import { compose } from 'redux'
class DownloadRequests extends React.Component{
	render(){
		const { auth, requests } = this.props 
		let number = 1;
		console.log(requests)
		if(auth.uid == null) return <Redirect to='/signin' /> 
		return(
			<div className="DownloadRequest">
				<Container fluid>
					<Row>
						<Col lg='12'>
							<Card>
								<CardHeader> Download Requests </CardHeader>
								<CardBody>
									<Table bordered responsive size="sm" hover>
										<thead>
											<tr>
												<th> No </th>
												<th> User </th>
												<th> Title </th>
												<th> Description </th>
												<th> Created At </th>
												<th> Status </th>
											</tr>
										</thead>
										<tbody>
											{requests && requests.map((request)=>{
												
												return(
													<tr>
														<td> {number++} </td>
														<td> {request.authorFirstName + ' ' + request.authorLastName} </td>
														<td> {request.title} </td>
														<td> {request.content}</td>
														<td></td>
														<td> {request.status} </td>
													</tr>
												)
											})}
											
										</tbody>
									</Table>
									<div className='text-center'>
										<Button> Download To Excel </Button>
									</div>
								</CardBody>
							</Card>
						</Col>
					</Row>
				</Container>
			</div>
		)
	}
}

const mapStateToProps = (state) => {
	console.log(state)
	return{
		auth: state.firebase.auth,
		requests: state.firestore.ordered.requests
	}
}

export default compose(
	connect(mapStateToProps),
	firestoreConnect([
		{
			collection: 'requests'
		}
	])
)(DownloadRequests)