import React from 'react'
import { CardBody, Form, FormGroup, Input, Label, Button } from 'reactstrap'
import { createInfo } from './../../../store/actions/InfoActions'
import { connect } from 'react-redux'
class CreateInformation extends React.Component{
	state = {
		title: '',
		content: '',
		date: ''
	}

	onChange = (e) => {
		this.setState({
			[e.target.id]: e.target.value
		})
	}

	onSubmit = (e) => {
		e.preventDefault();
		this.props.createInfo(this.state)
	}
	render(){
		return(
			<CardBody>
				<Form onSubmit={this.onSubmit}>
					<FormGroup>
						<Label> Title </Label>
						<Input type="text" id="title" onChange={this.onChange}/>
					</FormGroup>
					<FormGroup>
						<Label> Description </Label>
						<textarea type='text' className="form-control" id="content" onChange={this.onChange} />
					</FormGroup>
					<FormGroup>
						<Label> Date </Label>
						<Input type="date"  id="date" onChange={this.onChange} />
					</FormGroup>
					<FormGroup>
						<Button block> post </Button>
					</FormGroup>
				</Form>
			</CardBody>
		)
	}
}

const mapDispatchToProps = (dispatch) => {
	return{
		createInfo: (info) => dispatch(createInfo(info))
	}
}

export default connect(null, mapDispatchToProps)(CreateInformation)