const initState = {
	requestError: null
}

const RequestReducer = (state=initState, action)=>{
	switch(action.type){
		case "CREATE_SUCCESS":
			console.log('create sucess')
			return{
				...state,
				requestError: null
			}

		case "CREATE_ERROR":
			console.log('create error')
			return{
				...state,
				requestError: 'create error'
			}
		default:
			return state
	}
}

export default RequestReducer