import React from 'react'
import { signUp } from './../../store/actions/authActions'
import { Container, Row, Col, Card, CardBody, CardTitle, CardText, Form, FormGroup, Label, Input, Button, CustomInput } from 'reactstrap'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
class SignUp extends React.Component{
	state = {
		firstName: '',
		lastName: '',
		userName: '',
		division: '',
		email: '',
		password: '',
		nowDate: null
	}

	onChange = (e) => {
		this.setState({
			[e.target.id]: e.target.value
		})
	}

	onSubmit = (e) => {
		e.preventDefault();
		this.props.signUp(this.state)
	}

	componentDidMount(){
		setInterval(()=>{
			this.setState({
				nowDate: new Date().toLocaleTimeString()
			})
		}, 1000)
	}
	render(){
		const { nowDate } = this.state
		const { divisions, auth } = this.props
		if(auth.uid != null) return <Redirect to='/' />
		return(
			<div className='SignUp'>
				<Container fluid>
					<Row>
						<Col lg='5' md='5' sm='8' className='mx-auto'>
							<Card>
								<CardBody>
									<div className='Header_SignUp text-center'>
										<CardTitle> Portal </CardTitle>
										<div className='Box_Clock'>
											<CardText> {nowDate} </CardText>
										</div>
									</div>
									<div className='Form_SignUp'>
										<Form onSubmit={this.onSubmit}>
											<FormGroup>
												<Label>  &nbsp; </Label>
												<Input type='teks' id="firstName" onChange={this.onChange} required/>
												<span className="floating-label"> First Name </span>
											</FormGroup>
											<FormGroup>
												<Label>  &nbsp; </Label>
												<Input type='teks' id="lastName" onChange={this.onChange} required/>
												<span className="floating-label"> Last Name  </span>
											</FormGroup>
											<FormGroup>
												<Label>  &nbsp; </Label>
												<Input type='teks' id="userName" onChange={this.onChange} required/>
												<span className="floating-label"> Username </span>
											</FormGroup>
											<FormGroup>
									          <Label for="exampleCustomSelect">&nbsp;</Label>
									          <CustomInput type="select" id="division" name="customSelect" onChange={this.onChange}>
									            <option value="">Division</option>
									            {divisions.map((data)=>{
									            	return(
									            		<option>{data}</option>
									            	)
									            })}
									          </CustomInput>
				        					</FormGroup>
											<FormGroup>
												<Label>  &nbsp; </Label>
												<Input type='teks' id="email" onChange={this.onChange} required />
												<span className="floating-label"> Email </span>
											</FormGroup>
											<FormGroup>
												<Label>  &nbsp; </Label>
												<Input type='password' id="password" onChange={this.onChange} required/>
												<span className="floating-label"> Password </span>
											</FormGroup>
											<FormGroup className='text-center'>
												<Button block> Sign Up </Button>
											</FormGroup>
										</Form>
									</div>
								</CardBody>
							</Card>
							<div className='Blank_Space'>
								
							</div>
						</Col>
					</Row>
				</Container>
			</div>
		)
	}
}

const mapStateToProps = (state) => {
	return{
		divisions: state.divisions.divisions,
		auth: state.firebase.auth
	}
}

const mapDispatchToProps = (dispatch) => {
	return{
		signUp: (newUser) => dispatch(signUp(newUser))
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(SignUp)